table {
    margin: 0 auto;
    width: 500px;
    border-spacing: 0;
    font-family: 'Metrophobic', sans-serif;
}
th {
    background: rgb(44,22,86);
}
th, td {
    padding: 5px;
    width: 25%;
    border: 1px solid white;
    text-align: center;
}
th:hover, td:hover {
    background: rgb(111,97,135);
}

.statistics-container {
    padding: 10px 0;
    background: rgb(75,56,111);
    height: 400px;
    overflow-y: scroll;
    width: 100%;
    color: rgb(255,255,255);
    text-align: center;
}



