.history-container {
    background: rgb(75,56,111);
    padding: 10px 0;
    color: white;
    height: 400px;
    overflow-y: scroll;
}

.history-list-item {
    background: rgb(111,97,135);
}
.history-list-item:hover {
    background: rgb(75,56,111);
}

.history-romaji-generated-item {
    position: absolute;
    top: 0px;
    right: 0px;
    font-family: 'Metrophobic', sans-serif;
}
.history-romaji-input-item {
    position: absolute;
    bottom: 0px;
    right: 0px;
    font-family: 'Metrophobic', sans-serif;
}


