@media (max-width: 767px) {
    button {
        width: 80px;
    }
    .App {
        padding: 0;
    }
    .dashboard {
        padding: 10px 0;
    }

    .slider-container input[type="range"] {
        width: 50px;
    }

    .hiragana-list-item {
        height: 25px;
        width: 25px;
        font-size: 1.2em;
    }
    .romaji-input input[type="text"] {
        font-size: 1.2em;
    }


    table {
        width: 100%;
    }
}


