html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  background: rgb(218,215,224); 
}
#root {
  height: 100%;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
}

li {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 50px;
  height: 50px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App {
  max-width: 550px;
  margin: 0 auto;
  padding: 0 80px;
  height: 100%;
  -webkit-box-shadow: 0 0 0.5cm rgba(0,0,0,0.5);
          box-shadow: 0 0 0.5cm rgba(0,0,0,0.5);
  /* background: rgb(255,255,255); */
  background: rgb(44,22,86);
}

.hiragana-list, .history-list {
  margin: 10px 0;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
}

.hiragana-list-item {
  font-weight: 500;
  position:absolute;
  font-size: 1.5em;
  height: 50px;
  width: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
li:hover > .hiragana-list-item:hover {
  background: rgba(240,240,240,0.5);
  cursor: pointer;
}
li:hover > .hiragana-list-item:active {
  background: rgba(220,220,220,0.5);
  font-size: 1em;
}
.romaji-list-item {
  position: absolute;
  /* width: 50px;
  height: 50px; */
  bottom: 0px;
  right: 0px;
  /* display: flex;
  justify-content: flex-end;
  align-items: flex-end; */
  /* z-index: -1; */
  font-family: 'Metrophobic', sans-serif;
}

.romaji-input input[type="text"] {
  font-size: 1.5em;
  padding: 10px;
  letter-spacing: 0.5em;
}


